const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: '',
			type: '',
			typeText1: '所有额度',
			typeText2: '所有贷款分类',
			list: [],
			currentIndex: 0,
			mask: false,
			quota: '',
			keyShow: [],
			moneyList: [{
					name: '所有额度'
				},
				{
					name: '100-5000'
				},
				{
					name: '5000-2万'
				},
				{
					name: '2万-5万'
				},
				{
					name: '5万-10万'
				},
				{
					name: '10万以上'
				}
			],
			typeList: [{
					name: '所有贷款分类'
				},
				{
					name: '不查征信'
				},
				{
					name: '万元起步'
				},
				{
					name: '极速下款'
				},
				{
					name: '黑户必做'
				},
				{
					name: '白户贷款'
				},
				{
					name: '保险贷款'
				},
				{
					name: '私人借条'
				},
				{
					name: '淘宝授权'
				},
				{
					name: '车房贷款'
				},
				{
					name: '千元贷款'
				},
				{
					name: '代还信用卡'
				},
				{
					name: '社保公积金'
				},
				{
					name: '信用卡贷款'
				},
				{
					name: '芝麻分贷'
				},
				{
					name: '分期销售'
				},
				{
					name: '苹果ID贷'
				},
				{
					name: '黑卡系列'
				}
			],
		};
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
		this.getList()
	},
	methods: {
		goProduct(e) {
			let id = e.currentTarget.dataset.id
			uni.navigateTo({
				url:`/pages/productDetail?id=${id}`
			})
		},
		// 标签列表显示
		getTap(e) {
			this.type = e
			if (e == 1) {
				this.keyShow = this.moneyList
			} else {
				this.keyShow = this.typeList
			}
			this.mask = true
		},
		// 标签选择
		screenList(e) {
			this.currentIndex = e
			this.quota = this.keyShow[this.currentIndex].name
			if (this.type == 1) {
				this.typeText1 = this.keyShow[this.currentIndex].name
				this.typeText2 = '所有贷款分类'
			} else {
				this.typeText1 = '所有额度'
				this.typeText2 = this.keyShow[this.currentIndex].name
			}
			this.list = []
			this.mask = false
			this.getList()
		},
		// 隐藏标签列表
		hideModal() {
			this.mask = false
		},
		//获取列表数据
		getList() {
			uni.request({
				url: `${helper.requestUrl}/holes/categories-holes`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					// category_id: this.category_id,
					quota: this.quota,
					// page_size: this.page_size,
					// page: this.page
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
};
